#sidebar{-webkit-box-flex:0;-ms-flex:0 0 256px;flex:0 0 256px;background-color:#212121;padding:22px 0 0;max-height:calc(100vh - 112px);overflow:auto;overflow-x:hidden;-ms-overflow-style:-ms-autohiding-scrollbar}html.touchevents #sidebar{overflow-y:scroll;-webkit-overflow-scrolling:touch}#sidebar a.droppable{-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-transition:.3s;transition:.3s;-webkit-transform-origin:center left;transform-origin:center left;color:#fff;background-color:rgba(0,0,0,0.3)}#sidebar section{margin-bottom:32px}#sidebar section h1{text-transform:uppercase;letter-spacing:1px;padding:0 16px;margin-bottom:12px}#sidebar section h1 i{float:right}#sidebar section a{display:block;height:36px;line-height:36px;padding:0 12px 0 16px;border-left:4px solid transparent}#sidebar section a.active,#sidebar section a:hover{border-left-color:#ff7d2e;color:#fff;background:rgba(255,255,255,0.05)}#sidebar section a:hover{border-left-color:#c74b00}#sidebar section a::before{width:24px;display:inline-block;font-family:FontAwesome}#sidebar section a.home::before{content:"\f015"}#sidebar .new-ver{margin:16px;padding:16px;border:1px solid #a0a0a0;color:#fff;opacity:.3;font-size:.9rem;display:block;-webkit-transition:.3s;transition:.3s}#sidebar .new-ver:hover{opacity:.7}@media only screen and (max-width: 667px){#sidebar{position:fixed;height:calc(100vh - 112px);padding-bottom:64px;width:100%;z-index:99;top:48px;left:-100%;-webkit-transition:left .3s ease-in;transition:left .3s ease-in}#sidebar.showing{left:0}}



